@import 'variables';

@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-wide-tablet {
  @media (min-width: $wide-tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin on-big-screen {
  @media (min-width: $big-screen-min-width) {
    @content;
  }
}

@mixin button-style {
  border: none;
  padding: 0;
  margin: 0;
  border-radius: 8px;
  background: #f86800;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 0 0;

  color: #fff;
  text-align: center;
  font-family: inherit;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
}

@mixin like-button-style {
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  border-radius: 48px;
  border: 1px solid #b4bdc3;
}

@mixin properties {
  width: 100%;
  gap: 8px;
  display: flex;
  flex-direction: column;
  padding: 8px 0;
}

@mixin p {
  margin: 0;
  padding: 0;
  color: #89939a;
  font-family: inherit;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

@mixin span {
  color: #0f0f11;
  text-align: right;
  font-family: inherit;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}
